	import common from '../../../common/common.js'
	
	export default {
		data() {
			return {
				baseUrl:common.baseUrl,
				shopInfo:[],
				userid:'',
				invite:[],
			};
		},
		onLoad() {
			var that = this
			var shopInfo = uni.getStorageSync('shopInfo');
			that.shopInfo = shopInfo;
			
	
		},
		onShow(){
			var that = this
			var userid = uni.getStorageSync('userid');
			var shopInfo = uni.getStorageSync('shopInfo');
			that.shopInfo = shopInfo;
			that.userid = userid;
		},
		onPullDownRefresh() {
			var that = this
			uni.showNavigationBarLoading();
			console.log('下拉刷新中...');	
			
			uni.hideNavigationBarLoading();
			uni.stopPullDownRefresh();
		},
		methods:{
			//收货地址
			address(){
				var that = this
				uni.navigateTo({
					url: '/pages/shop/user/setting/address',
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			},
			
			//获取诉请人信息
			getInviteInfo(){
				var that = this
				var shopInfo = uni.getStorageSync('shopInfo');
				var invite_uid = shopInfo.invite_uid
				uni.request({
					url: common.sjApiUrl + 'user/getInviteInfo',
					method: 'GET',
					data: {
						invite_uid:invite_uid
					},
					success: res => {
						var data = res.data
						that.invite = data.invite
					},
					fail: () => {},
					complete: () => {}
				});
			},
			
			//推荐人
			goinvite(){
				var that = this
				uni.navigateTo({
					url: '/pages/user/setting/invite',
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			},
			
			
			//修改昵称
			modShopInfo(){
				var that = this
				uni.navigateTo({
					url: '/pages/shop/user/shopinfo',
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			},
			
			//修改头像
			changeLogo(){
				var that = this
				uni.chooseImage({
					count: 1, //默认9
					sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
					sourceType: ['album', 'camera'], //从相册选择
					success: function (res) {
						//console.log(JSON.stringify(res.tempFilePaths));
						const tempFilePaths = res.tempFilePaths;
						console.log(tempFilePaths);
						//return false;
						uni.uploadFile({
							url: common.sjApiUrl+'merchant/saveShopLogo', //仅为示例，非真实的接口地址
							filePath: tempFilePaths[0],
							name: 'file',
							formData: {
								'shop_id': that.shopInfo.shop_id
							},
							success: (uploadFileRes) => {
								console.log(uploadFileRes.data);
								var jsonDataStr = JSON.parse(uploadFileRes.data);
								var shopInfo = uni.getStorageSync('shopInfo');
								console.log(that.shopInfo)
								shopInfo['shop_logo'] = jsonDataStr.uploadfile;
								that.shopInfo = shopInfo;
								uni.setStorageSync('shopInfo',shopInfo);
// 								
// 								console.log(that.shopInfo)
							}
						});
					}
				});
			},
			
			//修改头像
			changeHaibao(){
				var that = this
				uni.chooseImage({
					count: 1, //默认9
					sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
					sourceType: ['album', 'camera'], //从相册选择
					success: function (res) {
						//console.log(JSON.stringify(res.tempFilePaths));
						const tempFilePaths = res.tempFilePaths;
						console.log(tempFilePaths);
						//return false;
						uni.uploadFile({
							url: common.sjApiUrl+'merchant/saveShopHaibao', //仅为示例，非真实的接口地址
							filePath: tempFilePaths[0],
							name: 'file',
							formData: {
								'shop_id': that.shopInfo.shop_id
							},
							success: (uploadFileRes) => {
								console.log(uploadFileRes.data);
								var jsonDataStr = JSON.parse(uploadFileRes.data);
								var shopInfo = uni.getStorageSync('shopInfo');
								console.log(that.shopInfo)
								shopInfo['shop_picture'] = jsonDataStr.uploadfile;
								that.shopInfo = shopInfo;
								uni.setStorageSync('shopInfo',shopInfo);
// 								
// 								console.log(that.shopInfo)
							}
						});
					}
				});
			},
			
			//修改密码
			modShopPassword(){
				var that = this
				uni.navigateTo({
					url: '/pages/shop/user/shoppassword',
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			},
			
			
			//退出当前账号
			userLoginQuit(){
				var that = this
				
				
				
				uni.showModal({
					title: '提示',
					content: '确定要退出当前账号吗？',
					showCancel: true,
					cancelText: '取消',
					confirmText: '确定',
					success: res => {
						console.log(res)
						if(res.confirm){
							uni.removeStorageSync('userid');
							uni.removeStorageSync('shopInfo');
							uni.showLoading();
							uni.reLaunch({
								url: '/pages/shop/userlogin/userlogin',
								success: res => {
									uni.hideLoading();
								},
								fail: () => {},
								complete: () => {}
							});
						}
						
											
					},
					fail: () => {},
					complete: () => {}
				});
			},
		}
	}
